import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import View from '../views/View.vue'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

  const routes = [
    {path:'/',redirect:'/login'},
  {
    path: '/home',
    name: 'Home',
    component: Home,
    children:[
      {
        path:'index',
        name:'index',
        componet:View
      }
    ]
  },{
    path: '/login',
    name: 'Home',
    component: Login,
  }
]

const router = new VueRouter({
  routes
})

export default router
