<template>
<v-app>
    <v-app-bar app><v-toolbar-title>登录页</v-toolbar-title></v-app-bar>
    <v-content class="text-center">
        <v-card  class="col-sm-4 text-center " style="left: 50%;top: 50%; transform: translate(-50%,-50%);
        ">
            <v-card-title>用户登录</v-card-title>

            <v-divider></v-divider>
            <v-card-text>
                <v-form>
                    <v-text-field label="用户名" v-model="formData.username"></v-text-field>
                    <v-text-field label="密码" v-model="formData.password" type="password"></v-text-field>
                    <v-card-text class="d-inline-block">
                        <v-btn  class="col-2" @click="login">登录</v-btn>
                        <v-btn>注册</v-btn>
                    </v-card-text>
                </v-form>
            </v-card-text>
        </v-card>
    </v-content>
</v-app>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return{
                formData:{
                    username:'wcs',
                    password:'123'
                }
            }
        },
        methods:{
            login(){
                if (this.formData.username){
                    this.$store.commit('setUsername',this.formData.username)
                    this.$router.push('/home')
                }

            }
        }
    }
</script>

